.claim-rect {
  fill: var(--accent-gray);
  /* border-radius: 100rem; */
  stroke: var(--bg);
}

.claim-text {
  width: 100%;
  height: 100%;
  font-size: 12px;
  text-align: center;
  fill: var(--bg);
  padding: 1rem;
}

.claim-link-rect {
  font-size: 12px;
  fill: var(--bg);
}

.claim-link-text {
  font-size: 12px;
  stroke: var(--accent-gray);
}

.objection-rect {
  fill: var(--accent-orange);
  stroke: var(--bg);
}

.objection-text {
  width: 100%;
  height: 100%;
  font-size: 12px;
  text-align: center;
  fill: var(--bg);
  padding: 1rem;
}
